import { Medico } from './../models/medico.model';
import { Hospital } from './../models/hospital.model';
import { Usuario } from './../models/usuario.models';
import { map } from 'rxjs/operators';
import { environment } from './../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';


const base_url = environment.base_url;

@Injectable({
  providedIn: 'root'
})
export class BusquedasService {

  constructor(private http: HttpClient) { }

  get token(): string {
    return localStorage.getItem('token') || '';
  }

  get headers(){
    return {
      headers: {
        'x-token': this.token
      }
    }
  }


  private transformarUsuarios( resultados: any[] ): Usuario[] {

    return resultados.map(
      user => new Usuario( user.google, user.nombre, user.email, user.img, user.role, user.uid )
    );
  }

  private transformarHospitales( resultados: any[] ): Hospital[] {

    return resultados;
  }

  private transformarMedicos( resultados: any[] ): Medico[] {

    return resultados;
  }

  buscar( tipo:  'medicos'|'usuarios'|'hospitales', termino: string ) {

    const url = `${ base_url }/todo/coleccion/${ tipo }/${ termino }`;
    return this.http.get<any[]>( url, this.headers )
      .pipe(
        map( (resp:any) => {

          switch ( tipo ) {

            case 'medicos':      
              return this.transformarMedicos( resp.resultado )

              case 'usuarios':      
              return this.transformarUsuarios( resp.resultado )
                
            case 'hospitales':      
              return this.transformarHospitales( resp.resultado )
          
            default:
              return [];
          }
        })
      );
  }

  buscarTodo( termino: string ){
    const url = `${ base_url }/todo/${ termino }`;
    return this.http.get( url, this.headers );
  }
}
